<template>
  <div>
    <div class="title">周末去哪</div>
    <ul>
      <li class="item border-bottom" 
          v-for="item of list" 
          :key="item.id"
      >
        <div class="item-img-wrapper">
          <img class="item-img" :src="item.imgUrl" />
        </div>  
        <div class="item-info">
          <p class="item-title">{{item.title}}</p>
          <p class="item-desc">{{item.desc}}</p>
        </div>       
      </li>
    </ul> 
  </div>  
</template>

<script>
export default {
  name: 'HomeWeekend',
  props: {
    list: Array
  }
}
    
</script>

<style lang="stylus" scoped>
  @import '~styles/mixins.styl'
  .title
    line-height: .8rem
    background: #eee
    text-indent: .2rem
  .item-img-wrapper
    overflow: hidden
    height: 0
    padding-bottom: 36.5%      
    .item-img
      width: 100%
  .item-info
    padding: .1rem
    .item-title
      line-height: .66rem
      font-size: .36rem
      ellipsis()
    .item-desc
      line-height: .7.5rem
      color: #ccc
      ellipsis()     
</style>


